.face-to-face-interviews {
    margin-bottom: 20px;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.face-to-face-interviews h2 {
    font-size: 24px;
    color: #2c3e50;
    margin-bottom: 10px;
}

.face-to-face-interviews p {
    font-size: 16px;
    color: #4f5d75;
    line-height: 1.6;
    margin-bottom: 10px;
}

.interview-link {
    font-size: 18px;
    color: #1a73e8;
    text-decoration: none;
    font-weight: bold;
}

.interview-link:hover {
    text-decoration: underline;
}

.facetoface-item {
    margin-bottom: 15px;
    padding: 15px;
    background-color: #f9fafb;
    border-radius: 8px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
    text-align: left;
}

.facetoface-item h3 {
    color: #2c3e50;
    font-size: 18px;
    margin-bottom: 10px;
}

.facetoface-item p {
    color: #4f5d75;
    font-size: 16px;
    line-height: 1.5;
}

.facetoface-item li {
    color: #4f5d75;
    margin-bottom: 5px;
}